import { Injectable, Optional, InjectionToken, Inject, } from '@angular/core';
import { LocaleService } from '@farris/ui-locale';
import { FARRIS_UPLOADSERVICE_LOCALEDATA } from './locale';
export interface FFileUploadAdaptUnifileConfig {
    rootId?: string;
    formId?: string;
    mode?: number;//0 代表正式，1代表临时
    options?:any;
}
export const FFileUploadAdaptUnifileConfigToken = new InjectionToken<FFileUploadAdaptUnifileConfig>('MFFileUploadAdaptUnifileConfig');
@Injectable({
    providedIn: 'root'
})
export class FFileUploadAdaptUnifileConfigService {
    private config = {
        rootId: '',
        formId: '',
        mode: 0
    } as FFileUploadAdaptUnifileConfig;
    private basePerfixStr='';    
    private localSerStorage:LocaleService;
    constructor(@Optional() @Inject(FFileUploadAdaptUnifileConfigToken) exConfig: FFileUploadAdaptUnifileConfig) {
        if (exConfig) {
            Object.assign(this.config, exConfig);
        }
    }
    /**
     * 获取config属性的值
     */
    getConfig() {
        return this.config;
    }
    /**
     * 设置config的属性值
     * @param key 
     * @param value 
     */
    setConfig(key, value) {
        this.config[key] = value;
    }
    /**
     * 设置基础路径
     * @param value 
     */
    setBasePath(value:string){
        this.basePerfixStr=value;
    }
    /**
     * 获取基础路径
     * @returns 
     */
    getBasePath(){
        return this.basePerfixStr;
    }
    setLocalState(localSer:LocaleService){
        if(this.localSerStorage){
            return true;
        }
        if(localSer&&!this.localSerStorage){
                localSer.setLocaleData(FARRIS_UPLOADSERVICE_LOCALEDATA);
                this.localSerStorage=localSer;
                return true;
        }
        return false;
    }
    /**
     * 获取多语言数据
     * 某些组件没有重新生成，要支持多语言
     * @param key 
     */
    getLocalStr(lanKey:string){
        let result="";
        let defaultLocalId='en';
        if(this.localSerStorage){
            result=this.localSerStorage.getValue(lanKey);
        }else{
            defaultLocalId= localStorage.getItem('languageCode')||'zh-CHS';
        }
        if(!result){
            result = lanKey.split('.').reduce((obj, key) => {
                if (obj) {
                    return obj[key];
                } else {
                    return null;
                }
            }, FARRIS_UPLOADSERVICE_LOCALEDATA[defaultLocalId]);
        }
        return result;
    }
};